import React, { Component } from 'react'
import axios from 'axios';
import location from '../pics/location.png'
import arrow from '../pics/arrow.png'
import clock from '../pics/clock.png'

export default class ListOfPosts extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             posts:[],
             errorMsg:''
        }
    }
    componentDidMount(){
        axios.get('https://jsonplaceholder.typicode.com/posts')
        .then(response=>{
            console.log(response)
            this.setState({posts:response.data})
        })
        .catch(error=>{
            console.log(error)
            this.setState({errorMsg:'Error retreiving data'})
        })
        

    }
    
    render() {
        const {posts, errorMsg}= this.state
        return (
            <div style={{width:"100%"}} className="container">
               {/* <p>List of Posts:</p> 
               {posts.length ? 
               posts.map(post=> <div key={post.id}>{post.title}</div> ) :null}
               { errorMsg ? <div>{errorMsg}</div> : null } */}
            <div style={{display:"grid",color:"white", height:50,width:"100%", backgroundColor:"rgb(57, 55, 119)"}} className="homeHeader">
                <h3 style={{margin:"auto"}}>HOME SCREEN</h3>
            </div>
            
            <div className="belowHeader">
            {/* the nav menu comes here */}
                <div className="listPosts">
                    <div style={{textAlign:"left"}} className="squarePost">
                        {posts.length ? 
                        posts.map(post=> <div key={post.id}>
                        <h4 style={{fontSize:"17pt"}}>{post.title}</h4>
                        <p style={{fontSize:"16pt"}}>{post.body}</p>
                        <div style={{display:"inline"}} className="locationIcons">
                            <img style={{width:"20px"}} src={location}></img>adress
                            <img style={{marginLeft:30, width:"20px"}} src={arrow}></img>distance
                            <img style={{marginLeft:20,width:"20px"}} src={clock}></img>time

                            {/* <p>adress</p> */}
                        </div>

                        <hr/>
                        </div> ) :null}
                    </div>
                </div>
            </div>
            </div>
        )
    }
}
